body {
	margin: 0 auto;
	background-color: #000;
	color: #fff;
	font-size: 18px;
	font-family: 'Open Sans', sans-serif;
}

hr {
	margin-right: 40px;
}

#loader {
	text-align: center;
}

#login {
	text-align: center;
}

#login button {
	width: 200px;
	height: 30px;
	border: none;
	background-color: #5F92DD;
	font-size: 17px;
	border-radius: 3px;
}

#home {
	text-align: center;
	margin-top: 10px;
	margin-bottom: 80px;
}

#home a {
	text-decoration: none;
	background-color: #5F92DD;
	padding: 5px 30px 5px 30px;
	margin-left: 10px;
}

#home button, #onlineF button {
	border: none;
	padding: 6px 30px 6px 30px;
	font-size: 18px;
	background-color: #5F92DD;
	color: #fff;
	margin-right: 40px;
}

#onlineF {
	margin-left: 40px;
}

#onlineF button {
	width: 150px;
}

#city_count {
	margin-top: 4px;
}

a, #home button, #onlineF button {
	transition: 0.4s;
}

#hide li input {
	border: 1px solid #69E563;
	transition: 0.4s;
	border-radius: 6px;
	padding-left: 5px;
}

#hide li input:hover {
	border: 1px solid rgb(36, 41, 48);
}

button:hover {
	color: #69E563 !important;
	cursor: pointer;
}

a:hover {
	color: #69E563;
}

#profile a:hover {
	color: #69E563;
}

#city_count div a {
	width: 200px;
	background-color: #5F92DD;
	text-decoration: none;
	margin-bottom: 5px;
}

@media (max-width: 800px) {
    .user_profile #profile img {
    	float: none;
    	width: 80%;
    	margin-right: 0;
    }
    .user_profile #profile {
    	text-align: center;
    	margin-left: 0;
    }

    .user_profile #lastseen {
    	max-width: 350px;
    	display: inline-block;
    }

    #home {
    	margin-bottom: 20px;
    }

    .user_profile #profile #lastseen font {
    	display: block;
    }

    #loader img {
    	width: 50%;
    }

    hr {
    	margin-right: 0;
    }

    .wrap #noteName {
    	margin-top: 20px !important;
    }

    .header p {
    	float: none !important;
    	display: none !important;
    }

    .search .input #input_name {
    	max-width: 60%;
    }

    .wrap #noteName div {
    	text-align: center;
    }

	.friends .search_user #onlineF {
		text-align: center;
		margin: 0;
	}

	.friends .search_user #home button, .friends .search_user #home a {
		width: 150px !important;
		margin: 5px;
	}

	.friends .search_user #onlineF button {
		margin: 5px;
	}

	.friends .search_user .user_search {
		text-align: center;
	}

	.friends .search_user .user_search ul {
		padding: 0;
		margin: 5px;
	}

	.wrap .user_search .new_info .online {
		display: none;
	}
}