.search {
	text-align: center;
	
}

#input {
	height: 30px;
	font-size: 17px;
	border: none;
	padding: 0;
	width: 300px;
}

.button ul {
	margin: 0;
	padding: 0;
	display: inline-block;
	width: 300px;
}

.button ul li a {
	text-decoration: none;
	display: block;
	width: 305px;
	border: 1px solid #5F92DD;
	border-radius: 6px;
}

.button ul li a:hover {
	border: 1px solid white;
}


.button li {
	list-style: none;
	text-align: center;
	font-size: 17px;
	margin-bottom: 2px;
}
.but {
	height: 30px;
	width: 100px;
	border: none;
	font-size: 17px;
	background-color: white;
	color: #000;
	border: 1px solid #69E563;
	border-radius: 0 6px 6px 0;
}

.butt {
	height: 30px;
	border: none;
	font-size: 17px;
	background-color: #5F92DD;
}

#input_name {
	width: 400px;
	height: 28px;
	font-size: 17px;
	margin-bottom: 15px;
	border: none;
	padding: 0;
	border: 1px solid #69E563;
	border-radius: 6px 0 0 6px;
	padding-left: 5px;
}

#input_name:hover {
	border: 1px solid #5F92DD;
	transition: 0.4s;
}

.but:hover {
	border: 1px solid #5F92DD;
	transition: 0.4s;
}


#load_info {
	width: 200px;
	height: 30px;
	font-size: 17px;
	border: none;
}

.show_menu-enter {
	opacity: 0;
}

.show_menu-enter-active {
	transition: opacity 750ms ease-out;
	opacity: 1;
}

.show_menu-exit-active {
	transition: opacity 750ms ease-out;
	opacity: 0;
}

.users-enter-active {
	animation: user 1s ease-in alternate-reverse;
}

.city-enter-active {
	animation: user 1s ease-in alternate-reverse;
}


@keyframes user {
	0% {
		opacity: 1;
		transform: translateX(0px);
	}
	30% {
		opacity: 0.7;
	}
	100% {
		transform: translateX(-200px);
		opacity: 0;
	}
}